




































import { Vue, Component } from 'vue-property-decorator'
import DB, { Entities } from '../database'
@Component
export default class FilterMgt extends Vue {
  private list: Entities.Filter[] = []
  private perPage = 10
  private currentPage = 1
  private count = 0
  private columns = [
    {
      label: 'ID',
      field: 'id',
      centered: true
    },
    {
      label: 'From Contract',
      field: 'contractName',
      centered: true
    },
    {
      label: 'Name',
      field: 'name',
      centered: true
    },
    {
      label: 'Address',
      field: 'address',
      centered: true
    }
  ]
  private async created() {
    this.onPageChange(1)
    this.count = await DB.filters.count()
  }
  edit(row: any) {
    this.$dialog.prompt({
      title: 'Edit quick view',
      message: 'Edit filter name',
      inputAttrs: {
        placeholder: 'Filter name',
        value: row.name,
        maxlength: 30,
        required: true
      },
      onConfirm: (value: string) => {
        // this.saveFilter(value)
        DB.filters.update(row.id, { name: value }).then(() => {
          this.onPageChange(this.currentPage)
        })
      }
    })
  }
  remove(row: any) {
    this.$dialog.confirm({
      title: 'Remove',
      message: `Are you sure want to remove ${row.name} contract`,
      cancelText: 'Cancel',
      confirmText: 'YES',
      type: 'is-danger',
      scroll: 'clip',
      onConfirm: () => {
        DB.filters.delete(row.id).then(() => {
          this.onPageChange(this.currentPage)
        })
      }
    })
  }

  private async onPageChange(page: number) {
    this.list = await DB.filters
      .offset((page - 1) * this.perPage)
      .limit(this.perPage)
      .toArray()
  }
}
